body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.theTop{
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 60px;
  background-color: lightskyblue;
  padding: 10px;
  box-shadow: 2px 10px 10px rgb(122, 160, 122);
  z-index: 4;
}

h1{
  float: left;
  margin:10px;
  font-size: 40px;
  font-style: normal;
  font-weight: 100;
  font-family: Noto Sans, sans-serif;
  color: white;
}

.logo{
  float: left;
  object-fit: contain; 
  max-height:110%;
  width:auto;
}

.body{
  display:grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 4em;
  z-index:2;
  height: 100vh;
  background-color: darkseagreen;
}

.gif{
  -webkit-animation: cssAnimation 0s 4s forwards;
          animation: cssAnimation 0s 4s forwards;
  visibility: visible;
  position:fixed;
  height: 100px;
  width: auto;
  top: 50%;
  left: 50%;
  margin-top: -50px;
  margin-left: -100px;
}
.nowLoading{
  -webkit-animation: cssAnimation 0s 4s forwards;
          animation: cssAnimation 0s 4s forwards;
  visibility: visible;
  top:0;
  position:fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0,20%);
}
@-webkit-keyframes cssAnimation {
  to   { visibility: hidden; }
}
@keyframes cssAnimation {
  to   { visibility: hidden; }
}

